import * as THREE from "three";
import { OrbitControls } from "three/examples/jsm/controls/OrbitControls";

import "./styles.css";

const renderer = new THREE.WebGLRenderer({
  canvas: document.querySelector("canvas"),
  antialias: true
});
renderer.setSize(window.innerWidth, window.innerHeight);

renderer.setPixelRatio(window.devicePixelRatio);

const clock = new THREE.Clock();
const scene = new THREE.Scene();
scene.background = new THREE.Color("black");
const camera = new THREE.PerspectiveCamera(
  75,
  window.innerWidth / window.innerHeight,
  0.1,
  1000
);

for (let i = 0; i < 30; i++) {
  const box = new THREE.Mesh(
    new THREE.BoxGeometry(),
    new THREE.MeshNormalMaterial()
  );
  scene.add(box);
  box.position.set(
    (Math.random() - 0.5) * 30,
    (Math.random() - 0.5) * 20,
    (Math.random() - 0.5) * 30
  );
}

// Assuming you have already set up a scene, renderer, and camera

// Load JSON data
const jsonUrl = "Public/file.json";

fetch(jsonUrl)
  .then((response) => response.json())
  .then((data) => {
    const animationData = data.animation;

    // Animate camera to each coordinate
    let tl = gsap.timeline();
    animationData.forEach((data) => {
      tl.to(camera.position, {
        x: data.x,
        y: data.y,
        z: data.z,
        duration: data.duration,
        ease: "power3.inOut"
      });
    });

    // Render animation
    function animate() {
      requestAnimationFrame(animate);
      renderer.render(scene, camera);
    }

    // Start animation when the data is loaded
    tl.eventCallback("onStart", animate);
  });
